import React from 'react';
import { PropTypes } from 'prop-types';
import SectionHeading from  '../section-heading/index.js';
import IntroText from  '../intro-text/index.js';
import PhotoWall from '../photo-wall/index.js';
import './style.css';

//Renders Amenities element
const Amenities = (props) => {
    return (
      <section className="amenities">
            <SectionHeading 
             id="amenities" 
              heading={props.heading} 
            />
            <IntroText 
              intro={props.introOne} 
            />
            <PhotoWall />
            <IntroText 
              intro={props.introTwo}
            />
        </section>
    )
}

Amenities.propTypes = {
  heading: PropTypes.string.isRequired,
  introOne: PropTypes.string.isRequired,
  introTwo: PropTypes.string.isRequired
}

Amenities.defaultProps = {
  heading: 'Amenities',
  introOne: 'Settle down in Manhattan without settling for anything less than what you always wanted. Like an in-house bike room, children\'s playroom, grocery market and outdoor yoga space. Skylar features 126 residences from spacious studios to 4 bedroom apartments, starting at $995,000. The award with you in mind', 
  introTwo: 'We invite you to join a warm and welcoming place, nestled in an ideal pocket of West Harlem. Get in on the neighborly social scene, and find everything you need nearby. You can stop by the local teashop or meet up with friends at your favorite wine bar. You\'ll loevery last alluring feature of Skylar.'
}

export default Amenities;


