import React from 'react';
import { PropTypes } from 'prop-types';
import SectionHeading from  '../section-heading/index.js';
import './style.css';

//Renders Location element
const Location = (props) => {
  let intro = props.highlights.intro,
    highlightList = props.highlights.list;

  return (
    <section className={props.heading.toLowerCase()} >
      <SectionHeading 
        id={props.heading} 
        heading={props.heading} 
      />
       <hr />
      <figure>
        <div 
          className="map" 
          style={{backgroundImage: `url(${props.src})`}}>
         </div>
        <figcaption className="highlights">
          <p>{intro}</p>
          <ol>
            {
              highlightList.map((item, key) => {
                return <li key={key}>{item}</li>;
              })
            }
          </ol>
        </figcaption>
      </figure>
    </section>
  )
}

Location.propTypes = {
  heading: PropTypes.string.isRequired,
  src: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired,
  highlights: PropTypes.shape({
    intro: PropTypes.string.isRequired,
    list: PropTypes.array.isRequired
  })
}

Location.defaultProps = {
  heading: 'Location',
  src: '/images/features/map-feature-image.jpg', 
  alt: 'Skylar Map Location',
  highlights: {
    intro: 'Conveniently located on flourishing Frederick Douglass Boulevard, Skylar offers distinct, local boutiques, as well as bars and restaurants, all just a short walk away. Plus, you can easily hop on the express train to access all of Manhattan in minutes:',
    list: [
      'At the crossroads of culture and a vibrant culinary scene',
      'Accessible to some of the best Harlem restaurants and brunch spots like Lido, The Cecil and Harlem Tavern',
      'Close by great bars and wine shops like Vinateria, Mess Hall, and 67 Orange Street',
      'Near natural escapes like Morningside Park and Central Park',
      'A short walk to museums, historic landmarks and Columbia University'
    ]
  }
}

export default Location;





