import React, { Component } from 'react';
import { PropTypes } from 'prop-types';
import './style.css';

//Renders PhotoWall element
class PhotoWall extends Component {
  constructor(props) {
    super(props);
    this.state = {
      iterator: 0,
      active: 0
    }
    this.animate = null;
    this.animatePhotoWall = this.animatePhotoWall.bind(this);
    this.resetPhotoWall = this.resetPhotoWall.bind(this);
  }
  componentDidMount() {
    const amenitiesSection = document.querySelector('section.amenities'),
              captions = document.querySelectorAll('figcaption');

    window.addEventListener('scroll', () => {
      if (
        window.scrollY > amenitiesSection.offsetTop && 
        window.scrollY < (amenitiesSection.offsetTop + amenitiesSection.offsetHeight) &&
        this.state.active === 0
      ) {
        this.animate = setInterval(() => {
          this.animatePhotoWall(captions);
        }, 2000);
        this.setState(() => ( {active: 1} ));
      } 
      if (
        (window.scrollY < amenitiesSection.offsetTop ||
        window.scrollY > (amenitiesSection.offsetTop + amenitiesSection.offsetHeight)) &&
        this.state.active === 1
      ) {
        this.resetPhotoWall(captions);
        clearInterval(this.animate);
        this.setState(() => ( {active: 0} ));
      }
    });
  }
  resetPhotoWall(captions) {
    captions.forEach(caption => {
      caption.classList.remove('show-caption');
    });
  }
  animatePhotoWall(captions) {
    const captionsLength = captions.length;

    this.resetPhotoWall(captions);
    captions[this.state.iterator].classList.add('show-caption');
    
    if(this.state.iterator < (captionsLength - 1)) { 
      this.setState((prevState) => ( {iterator: prevState.iterator += 1} ));
      
    }
    if(this.state.iterator ===  (captionsLength - 1)) { 
      this.setState(() => ( {iterator: 0} ));
    }
  }
  render() {
    return (
      <div className="photo-wall">
          <div className="colum-left">
            <figure>
              <img 
                src={this.props.collage[0].src} 
                alt={this.props.collage[0].alt} 
              />
              <figcaption>
                <span>{this.props.collage[0].caption}</span>
              </figcaption>
            </figure>
          </div>
          <div className="colum-center">
            <div className="row-top">
              <figure>
                <img 
                  src={this.props.collage[1].src} 
                  alt={this.props.collage[1].alt} 
                />
                <figcaption>
                  <span>{this.props.collage[1].caption}</span>
                </figcaption>
              </figure>
            </div>
            <div className="row-bottom">
              <figure>
                  <img 
                    src={this.props.collage[2].src} 
                    alt={this.props.collage[2].alt}
                  />
                  <figcaption>
                    <span>{this.props.collage[2].caption}</span>
                  </figcaption>
                </figure>
            </div>
          </div>
          <div className="colum-right">
            <figure>
                <img 
                  src={this.props.collage[3].src} 
                  alt={this.props.collage[3].alt}
                />
                <figcaption>
                  <span>{this.props.collage[3].caption}</span>
                </figcaption>
            </figure>
          </div>
      </div>
    )
  }
}

PhotoWall.propTypes = {
  collage: PropTypes.shape({
    src: PropTypes.string.isRequired,
    alt: PropTypes.string.isRequired,
    caption: PropTypes.string.isRequired
  })
}

PhotoWall.defaultProps = {
  collage: [
    {
      src: '/images/features/collage-park-scene.jpg', 
      alt: 'Serene Park',
      caption: 'Enjoy nature in a variety of nearby parks. '
    },
    {
      src: '/images/features/collage-dog-scene.jpg', 
      alt: 'Dog Resting',
      caption: 'Watch the bustle outside, in quiet comfort..'
    },
    {
      src: '/images/features/collage-bakery-scene.jpg', 
      alt: 'Local Bakery',
      caption: 'An abundnace of places to eat.'
    },
    {
      src: '/images/features/collage-harvest-scene.jpg', 
      alt: 'Farmers Market',
      caption: 'Farmers market, take your pick!'
    }
  ]
}

export default PhotoWall;


