import React, { Component } from 'react';
import Header from  '../header/index.js';
import Marquee from '../marquee/index.js';
import Amenities from '../amenities/index.js';
import Location from '../location/index.js';
import ContactForm from '../contact-form/index.js';
import Footer from  '../footer/index.js';
import './style.css';

//Renders App element
class App extends Component {
  constructor(props) {
    super(props);
    this.navSectionScroll = this.navSectionScroll.bind(this);
  }
  navSectionScroll(event) {
    event.preventDefault();

    const getNavSection = event.target.href.split('#'),
    setNavSection = document.querySelector(`.${getNavSection[1]}`);

    window.scrollTo({
      top: setNavSection.offsetTop,
      left: 0,
      behavior: 'smooth'
    });
  }
  render() {
    return (
      <div className="main-content">
        <Header 
          navSectionScroll={this.navSectionScroll} 
        />
        <Marquee />
        <Amenities />
        <Location />
        <ContactForm />
        <Footer />
      </div>
    );
  }
}

export default App;



