import React from 'react';
import Navigation from  '../navigation/index.js';
import ContactText from  '../contact-text/index.js';
import './style.css';

//Renders Header element
const Header = (props) => {
    return (
      <header>
        <Navigation 
          navSectionScroll={props.navSectionScroll} 
        />
        <ContactText />
      </header>
    )
}

export default Header;



