import React from 'react';
import { PropTypes } from 'prop-types';
import SectionHeading from  '../section-heading/index.js';
import './style.css';

//Renders Marquee element
const Marquee = (props) => {
  return (
    <section className={props.heading.toLowerCase()}>
      <div className="marquee-banner">
				<div className="pre-heading">
					{props.preHeading}
				</div>
				<SectionHeading 
            id={props.heading}
            heading={props.heading}
        />
				<div  className="post-heading">
					{props.postHeading}
				</div>
				<div className="tagline">
					{props.tagline}
				</div>
			</div>
      <img 
        className="background" 
        src={props.src} 
        alt={props.alt} 
      />
  </section>
  )
}

Marquee.propTypes = {
  heading: PropTypes.string.isRequired,
  preHeading: PropTypes.string.isRequired,
  postHeading: PropTypes.string.isRequired,
  tagline: PropTypes.string.isRequired,
  src: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired
}

Marquee.defaultProps = {
  heading: 'Skylar',
  preHeading: '300 W 122',
  postHeading: 'Harlem NY',
  tagline: 'A neighborhood to call home.',
  src: '/images/marquee/marquee-image.jpg', 
  alt: 'Skylar Cityscape Marquee | 300 W 122 Harlem NY | A neighborhood to call home'
}

export default Marquee;



